import React, {
  useCallback,
  useState,
  useRef,
  useEffect,
  useMemo,
} from "react";
import fishicon from "../assets/fishicon.png";
import hookicon from "../assets/hookicon.png";
import newcatch from "../assets/newcatch.png";
import compass from "../assets/compass6.png";
import usePlacesAutocomplete, {
  getGeocode,
  getLatLng,
} from "use-places-autocomplete";
import axios from "axios";
import NewCatch from "../components/NewCatch";

// // import Google Maps
import {
  GoogleMap,
  useGoogleMap,
  Marker,
  InfoBox,
  Circle,
  InfoWindow,
  useJsApiLoader,
  StandaloneSearchBox,
  Autocomplete,
} from "@react-google-maps/api";

function CatchMap({ mapCenter, setMapCenter }) {
  const [dragEvent, setDragEvent] = useState(false);
  const libraries = ["places"];
  const mapRef = useRef();
  const containerStyle = {
    width: "1000px",
    height: "600px",
  };

  const options = {
    // styles: MapStyles,
    // disableDefaultUI: true,
    zoomControl: true,
    scaleControl: true,
    // mapTypeControl: true,
    mapTypeId: "terrain",
  };
  // loads google map api's script
  const { isLoaded } = useJsApiLoader({
    id: "google-map-script",
    googleMapsApiKey: import.meta.env.VITE_GOOGLE_MAPS_API_KEY,
    libraries,
  });

  const [map, setMap] = useState(null);
  // loads map
  const onMapLoad = useCallback((map) => {
    mapRef.current = map;
  }, []);

  const onUnmount = useCallback(function callback(map) {
    setMap(null);
  }, []);
  // state for adding new marker
  const [markers, setMarkers] = useState([]);
  // state for changing which marker has an info window open
  const [activeMarker, setActiveMarker] = useState(null);
  // function to to set/change info window popup
  const handleActiveMarker = (marker) => {
    if (marker === activeMarker) {
      return;
    }
    setActiveMarker(marker);
  };

  let markerCount = 26;
  // hook for generating a new catch icon on map (used with Add Catch button)
  const [newFishMarker, setNewFishMarker] = useState(false);

  /* THIS IS STATE CODE FOR THE SEARCHBOX FUNCTIONALITY */
  //setting state for autocomplete instance
  const [autocomplete, setAutocomplete] = useState(null);

  //Pan To function declaration takes a latitude and a longitude
  const panTo = React.useCallback(({ lat, lng }) => {
    mapRef.current.panTo({ lat, lng });
    mapRef.current.setZoom(11);
  }, []);

  // onLoad callback called when autocomplete has loaded.
  const onLoad = (autocomplete) => {
    //setting the instance of autocomplete
    setAutocomplete(autocomplete);
  };

  // onPlaceChanged is called when a user selects a location from the suggestions in the box dropdown.
  const onPlaceChanged = () => {
    if (autocomplete !== null) {
      let panLat = autocomplete.getPlace().geometry.location.lat();
      let panLng = autocomplete.getPlace().geometry.location.lng();
      let LatLng = { lat: panLat, lng: panLng };
      setMapCenter(LatLng);
      panTo(LatLng);
    } else {
      console.log("Autocomplete is not loaded yet");
    }
  };

  const [newCatchLat, setNewCatchLat] = useState(34.48686532);
  const [newCatchLng, setNewCatchLng] = useState(-82.880513);

  const [allFishData, setAllFishData] = useState([]);

  const [newCatchSaved, setNewCatchSaved] = useState(false);

  useEffect(() => {
    axios.get("fish_data").then((response) => {
      let data = response["data"]["data"];
      let convertedData = JSON.parse(data);
      setAllFishData(convertedData);
    });
  }, [newCatchSaved]);

  const [buttonText, setButtonText] = useState("Add a New Catch");

  const changeText = (text) => setButtonText(text);

  return isLoaded ? (
    <div class="MapPage">
      <h2 id="MapTitle">See What Local Anglers Have Been Catching Near You!</h2>
      <div id="MapInstructions">
        <p>
          Find your local fishing location using the search bar below or click
          the compass icon to find your location by your device.
        </p>
        <p>
          {" "}
          Then click the "Add a New Catch" button to add a new fish to the map.
        </p>
        <p> Drag the fish icon to the location where you landed your fish.</p>
        <p>
          {" "}
          Double click the icon to add details about the catch and save it to
          the map.
        </p>
        <button
          onClick={() => {
            if (newFishMarker == false) {
              setNewFishMarker(true);
              changeText("Remove New Marker");
            } else {
              setNewFishMarker(false);
              changeText("Add a New Catch");
            }
          }}
        >
          {buttonText}
        </button>
      </div>
      <br />
      <div id="MapBox">
        <img
          src={compass}
          id="geolocate"
          onClick={() => {
            alert("Ensure Location Services Are Enabled in This Browser");
            // try {
            navigator.geolocation.getCurrentPosition(
              (position) => {
                panTo({
                  lat: position.coords.latitude,
                  lng: position.coords.longitude,
                });
                setMapCenter({
                  lat: position.coords.latitude,
                  lng: position.coords.longitude,
                });
              },
              () => null
            );
          }}
        />
        <GoogleMap
          mapContainerStyle={containerStyle}
          center={
            mapCenter
              ? mapCenter
              : setMapCenter({ lat: 34.48686532, lng: -82.880513 })
          }
          zoom={11}
          options={options}
          onLoad={onMapLoad}
          onUnmount={onUnmount}
          onClick={() => {
            setActiveMarker(null);
          }}
        >
          <Autocomplete onLoad={onLoad} onPlaceChanged={onPlaceChanged}>
            <input
              type="text"
              placeholder="Enter a fishing location"
              id="searchbox"
              style={{
                boxSizing: `border-box`,
                border: `1px solid black`,
                width: `190px`,
                height: `32px`,
                padding: `0 12px`,
                borderRadius: `3px`,
                boxShadow: `0 2px 6px rgba(0, 0, 0, 0.3)`,
                fontSize: `14px`,
                outline: `none`,
                textOverflow: `ellipses`,
                position: "absolute",
                // display: "flex",
                left: "40%",
                // marginLeft: "-10x",
                top: "10px",
              }}
            />
          </Autocomplete>
          {allFishData.map(({ pk, fields }) => (
            <Marker
              key={pk}
              position={{
                lat: parseFloat(fields.latitude),
                lng: parseFloat(fields.longitude),
              }}
              icon={fishicon}
              onClick={() => handleActiveMarker(pk)}
            >
              {activeMarker == pk ? (
                <InfoWindow onCloseClick={() => setActiveMarker(null)}>
                  <div id="infoWindow">
                    <b>
                      <div>Species: {fields.species}</div>
                    </b>
                    <b>
                      <div>Date: {fields.date}</div>
                    </b>
                    <b>
                      <div>Method: {fields.fishing_method}</div>
                    </b>
                    <b>
                      <div>Depth: {fields.depth} ft.</div>
                    </b>
                    <b>
                      <div>Weight: {fields.weight} lbs.</div>
                    </b>
                    <b>
                      <div>Length: {fields.length} in.</div>
                    </b>
                    <b>
                      <div>Field Notes:</div>
                    </b>
                    <b>
                      <div>{fields.notes}</div>
                    </b>
                    <br />
                    <img id="infoPic" src={`static/${fields.catch_picture}`} />
                  </div>
                </InfoWindow>
              ) : null}
            </Marker>
          ))}
          {newFishMarker && (
            <Marker
              key={markerCount}
              position={mapCenter}
              icon={newcatch}
              draggable={true}
              onDragEnd={(event) => {
                // Setting state to true for the add new catch popup window
                setDragEvent(true);
                setNewCatchLat(event.latLng.lat());
                setNewCatchLng(event.latLng.lng());
                // newCatchLat and newCatchLng have both been updated, but don't reflect changes until double click
              }}
              onDragStart={(event) => {
                setDragEvent(false);
                console.log(">>>>drag start");
              }}
              onDblClick={(event) => {
                console.log(
                  "We can have the user use the double click property to set his icon and bring up the fish data form"
                );
                console.log(
                  "My catch coord states have been set to  " +
                    newCatchLat +
                    " and " +
                    newCatchLng
                );
              }}
            />
          )}
          <></>
        </GoogleMap>
      </div>
      {dragEvent ? (
        <NewCatch
          newCatchLat={newCatchLat}
          newCatchLng={newCatchLng}
          panTo={panTo}
          allFishData={allFishData}
          setNewFishMarker={setNewFishMarker}
          changeText={changeText}
          setMapCenter={setMapCenter}
          mapCenter={mapCenter}
          setNewCatchSaved={setNewCatchSaved}
        />
      ) : null}
    </div>
  ) : (
    <></>
  );
}

export default CatchMap;
